.bones-pic {
  display: flex;
  align-items: flex-start;
  padding: 0.266667rem 0.4rem;
  border-bottom: 1px solid #f5f5f5;
  font-size: 0;
}
.bones-pic .left-box {
  width: 3.013333rem;
  height: 2.08rem;
  margin-right: 0.266667rem;
  background-color: #f5f5f5;
}
.bones-pic .right-container .fir-line {
  width: 6.386667rem;
}
.bones-pic .right-container .sec-line {
  width: 2.666667rem;
  margin-top: 0.2rem;
}
.bones-pic .right-container .thi-line {
  width: 4rem;
  margin-top: 0.266667rem;
}
.bones-pic .right-container p {
  height: 0.32rem;
  background-color: #f5f5f5;
}
