.VirtualKey {
  margin: 0;
  width: 100%;
  left: 0;
}
.keybMask {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(21, 20, 20, 0.02);
}
.VirtualKey li {
  float: left;
  width: 10%;
  padding: 0 0.15rem 0.15rem 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  height: 1.25rem;
  font-size: 0.383rem;
}
.VirtualKey li a {
  text-align: center;
  display: block;
  width: 100%;
  height: 100%;
  line-height: 1.11rem;
  border-radius: 0.3em;
  background: #fff;
  -webkit-box-shadow: 0px 1px 1px #aaa;
  box-shadow: 0px 1px 1px #aaa;
  color: #5c5c5c;
}
.VirtualKey .sureBtn {
  height: 1.11rem;
  line-height: 1.11rem;
  text-align: center;
  padding: 0px 0.15rem 0.15rem 0;
}
.VirtualKey .sureBtn a {
  display: block;
  border-radius: 0.3em;
  background: #a5a5a5;
  font-size: 0.45rem;
  color: #fff;
}
